.section-subscribe {
    background-color: #ffd1d7;
}

.subscribe {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 27px 10px 22px;

    &__desc {
        margin-bottom: 12px;

        @media screen and (max-width: 570px){
            font-size: 14px;
        }
    }

    &__input-wrapper {
        width: 40%;
        min-width: 245px;
        background-color: #fff;
        padding: 12px 14px;
        border-radius: 5px;

        display: flex;
        justify-content: space-between;
        gap: 10px;

        input {
            width: 100%;
        }
    }
}

.input-submit {
    img {
        transition: transform 0.15s ease-in;
    }

    &:hover {
        img {
            transform: translateX(4px);
        }
    }
}
